* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

#search-box {
    font: inherit;
    padding: 8px;
    max-width: 400px;
    margin-bottom: 16px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    margin-top: 4px;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
}

#search-box:hover,
#search-box:focus {
    border-color: #2196f3;
    fill: #2196f3;
}

.wrapper {
    display: flex;
    align-items: center;
}

.title {
    margin-left: 30px;
    font-size: 40px;
}

.item {
    font-size: 24px;
    color: darkblue;
}

.value {
    font-weight: bold;
    font-size: 20px;
    margin-left: 20px;
    color: #212121;
}

.list {
    display: flex;
    align-items: center;
}

.flag {
    margin-right: 30px;
}

.card {
    background: #ffffff;
    border-radius: 16px 16px 16px 16px;
    width: 40%;
    padding: 40px;
    display: block;
    cursor: pointer;
}